import React, { Component } from 'react';
import {
  Card,
  Button,
  Modal,
  Form,
  Input,
  Radio,
  DatePicker,
  Select
} from 'antd';
import axios from '../../axios';
import Utils from '../../utils/utils';
import ETable from '../../components/ETable';
import BaseForm from '../../components/baseForm';
import moment from 'moment';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const Option = Select.Option;
const TextArea = Input.TextArea;

export default class User extends Component {
  state = {
    isVisible: false
  };
  params = {
    page: 1
  };
  formList = [
    {
      type: 'INPUT',
      label: '',
      field: 'user_name',
      placeholder: '',
      width: 100
    },
    {
      type: 'INPUT',
      label: '',
      field: 'user_mobile',
      placeholder: '',
      width: 100
    },
    {
      type: 'DATEPICKER',
      label: '',
      field: 'user_date',
      placeholder: ''
    }
  ];

  componentDidMount() {
    this.requestList();
  }

  /**
   * 
   */
  handleFilter = params => {
    this.params = params;
    this.requestList();
  };

  /**
   * 
   */
  requestList = () => {
    axios.requestList(this, '/user/list', this.params);
  };

  /**
   * 
   */
  handleOperate = type => {
    let item = this.state.selectedItem;
    if (type == 'create') {
      this.setState({
        type,
        isVisible: true,
        title: ''
      });
    } else if (type == 'edit') {
      if (!item) {
        Modal.info({
          title: '',
          content: ''
        });
        return;
      }
      this.setState({
        type,
        isVisible: true,
        title: '',
        userInfo: item
      });
    } else if (type == 'detail') {
      this.setState({
        type,
        isVisible: true,
        title: '',
        userInfo: item
      });
    } else {
      if (!item) {
        Modal.info({
          title: '',
          content: ''
        });
        return;
      }
      let _this = this;
      Modal.confirm({
        title: '',
        content: '',
        onOk() {
          axios
            .ajax({
              url: '/user/delete',
              data: {
                params: {
                  id: item.id
                }
              }
            })
            .then(res => {
              if (res.code == 0) {
                _this.setState({
                  isVisible: false
                });
                _this.requestList();
              }
            });
        }
      });
    }
  };

  /**
   *  
   */
  handleSubmit = () => {
    let type = this.state.type;
    let data = this.userForm.props.form.getFieldsValue();
    axios
      .ajax({
        url: type == 'create' ? '/user/add' : '/user/edit',
        data: {
          params: data
        }
      })
      .then(res => {
        if (res.code == 0) {
          this.userForm.props.form.resetFields();
          this.setState({
            isVisible: false
          });
          this.requestList();
        }
      });
  };

  render() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '',
        dataIndex: 'username'
      },
      {
        title: '',
        dataIndex: 'sex',
        render(sex) {
          return sex == 1 ? '' : '';
        }
      },
      {
        title: '',
        dataIndex: 'state',
        render(state) {
          return {
            '1': '',
            '2': '',
            '3': '',
            '4': 'FE',
            '5': ''
          }[state];
        }
      },
      {
        title: '',
        dataIndex: 'interest',
        render(interest) {
          return {
            '1': '',
            '2': '',
            '3': '',
            '4': '',
            '5': '',
            '6': '',
            '7': '',
            '8': ''
          }[interest];
        }
      },
      {
        title: '',
        dataIndex: 'birthday'
      },
      {
        title: '',
        dataIndex: 'address'
      },
      {
        title: '',
        dataIndex: 'time'
      }
    ];

    let footer = {};
    if (this.state.type == 'detail') {
      footer = {
        footer: null
      };
    }
    return (
      <div>
        <Card>
          <BaseForm formList={this.formList} filterSubmit={this.handleFilter} />
        </Card>
        <Card style={{ marginTop: 10 }} className="operate-wrap">
          <Button
            type="primary"
            icon="plus"
            onClick={() => this.handleOperate('create')}
          >
            
          </Button>
          <Button
            type="primary"
            icon="edit"
            onClick={() => this.handleOperate('edit')}
          >
            
          </Button>
          <Button type="primary" onClick={() => this.handleOperate('detail')}>
            
          </Button>
          <Button
            type="primary"
            icon="delete"
            onClick={() => this.handleOperate('delete')}
          >
            
          </Button>
        </Card>
        <div className="content-wrap">
          <ETable
            updateSelectedItem={Utils.updateSelectedItem.bind(this)}
            columns={columns}
            dataSource={this.state.list}
            selectedRowKeys={this.state.selectedRowKeys}
          />
        </div>
        <Modal
          title={this.state.title}
          visible={this.state.isVisible}
          onOk={this.handleSubmit}
          onCancel={() => {
            this.userForm.props.form.resetFields();
            this.setState({
              isVisible: false
            });
          }}
          width={600}
          {...footer}
        >
          <UserForm
            type={this.state.type}
            userInfo={this.state.userInfo}
            wrappedComponentRef={inst => (this.userForm = inst)}
          />
        </Modal>
      </div>
    );
  }
}

class UserForm extends Component {
  getState = state => {
    return {
      '1': '',
      '2': '',
      '3': '',
      '4': 'FE',
      '5': ''
    }[state];
  };
  render() {
    let type = this.props.type;
    let userInfo = this.props.userInfo || {};
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: { span: 5 },
      wrapperCol: { span: 19 }
    };
    return (
      <Form layout="horizontal">
        <FormItem label="" {...formItemLayout}>
          {type == 'detail'
            ? userInfo.username
            : getFieldDecorator('user_name', {
                initialValue: userInfo.username
              })(<Input type="text" placeholder="" />)}
        </FormItem>
        <FormItem label="" {...formItemLayout}>
          {type == 'detail'
            ? userInfo.sex == 1
              ? ''
              : ''
            : getFieldDecorator('sex', {
                initialValue: userInfo.sex
              })(
                <RadioGroup>
                  <Radio value={1}></Radio>
                  <Radio value={2}></Radio>
                </RadioGroup>
              )}
        </FormItem>
        <FormItem label="" {...formItemLayout}>
          {type == 'detail'
            ? this.getState(userInfo.state)
            : getFieldDecorator('state', {
                initialValue: userInfo.state
              })(
                <Select>
                  <Option value={1}></Option>
                  <Option value={2}></Option>
                  <Option value={3}></Option>
                  <Option value={4}>FE</Option>
                  <Option value={5}></Option>
                </Select>
              )}
        </FormItem>
        <FormItem label="" {...formItemLayout}>
          {type == 'detail'
            ? userInfo.birthday
            : getFieldDecorator('birthday', {
                initialValue: moment(userInfo.birthday)
              })(<DatePicker />)}
        </FormItem>
        <FormItem label="" {...formItemLayout}>
          {type == 'detail'
            ? userInfo.address
            : getFieldDecorator('address', {
                initialValue: userInfo.address
              })(<TextArea rows={3} placeholder="" />)}
        </FormItem>
      </Form>
    );
  }
}

UserForm = Form.create({})(UserForm);
