import React, { Component } from 'react';
import { Card, Form, Button, Input, Icon, message, Checkbox } from 'antd';

const FormItem = Form.Item;

class FormLogin extends Component {
  handleSubmit = () => {
    let userInfo = this.props.form.getFieldsValue();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        message.success(
          `${userInfo.userName} ${userInfo.userPwd}`
        );
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div>
        <Card title="">
          <Form layout="inline">
            <FormItem>
              <Input placeholder="Please type loginname" />
            </FormItem>
            <FormItem>
              <Input placeholder="Please type password" />
            </FormItem>
            <FormItem>
              <Button type="primary">Login</Button>
            </FormItem>
          </Form>
        </Card>
        <Card title="" style={{ marginTop: 15 }}>
          <Form style={{ width: 300 }}>
            <FormItem>
              {getFieldDecorator('userName', {
                initialValue: '',
                rules: [
                  {
                    required: true,
                    message: ''
                  },
                  {
                    min: 5,
                    max: 10,
                    message: ''
                  },
                  {
                    pattern: new RegExp('^\\w+$', 'g'),
                    message: ''
                  }
                ]
              })(
                <Input
                  prefix={<Icon type="user" />}
                  placeholder="Please type loginname"
                />
              )}
            </FormItem>
            <FormItem>
              {getFieldDecorator('userPwd', {
                initialValue: '',
                rules: []
              })(
                <Input
                  prefix={<Icon type="lock" />}
                  placeholder="Please type password"
                />
              )}
            </FormItem>
            <FormItem>
              {getFieldDecorator('remember', {
                valuePropName: 'checked',
                initialValue: true
              })(<Checkbox></Checkbox>)}
              <a href="/" style={{ float: 'right' }}>
                
              </a>
            </FormItem>
            <FormItem>
              <Button type="primary" onClick={this.handleSubmit}>
                Login
              </Button>
            </FormItem>
          </Form>
        </Card>
      </div>
    );
  }
}

export default Form.create()(FormLogin);
