import React, { Component } from 'react';
import {
  Card,
  Form,
  Button,
  Input,
  Checkbox,
  Radio,
  Select,
  Switch,
  DatePicker,
  TimePicker,
  Upload,
  Icon,
  message,
  InputNumber
} from 'antd';
import moment from 'moment';

const FormItem = Form.Item;
const { Option } = Select;
const { TextArea } = Input;

class FormRegister extends Component {
  state = {};

  handleSubmit = () => {
    let userInfo = this.props.form.getFieldsValue();
    message.success(
      `${userInfo.userName} ${userInfo.userPwd}`
    );
  };

  getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  };

  handleChange = info => {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, imageUrl =>
        this.setState({
          userImg: imageUrl,
          loading: false
        })
      );
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: 24,
        sm: 4
      },
      wrapperCol: {
        xs: 24,
        sm: 12
      }
    };
    const offsetLayout = {
      wrapperCol: {
        xs: 24,
        sm: {
          span: 12,
          offset: 4
        }
      }
    };
    return (
      <div>
        <Card title="">
          <Form layout="horizontal">
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('userName', {
                initialValue: '',
                rules: [
                  {
                    required: true,
                    message: ''
                  }
                ]
              })(<Input placeholder="Please type loginname" />)}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('userPwd', {
                initialValue: ''
              })(<Input type="password" placeholder="Please type password" />)}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('sex', {
                initialValue: '1'
              })(
                <Radio.Group>
                  <Radio value="1"></Radio>
                  <Radio value="2"></Radio>
                </Radio.Group>
              )}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('age', {
                initialValue: 18
              })(<InputNumber />)}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('state', {
                initialValue: '2'
              })(
                <Select>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                  <Option value="3"></Option>
                  <Option value="4">FE</Option>
                  <Option value="5"></Option>
                </Select>
              )}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('interest', {
                initialValue: ['2', '5']
              })(
                <Select mode="multiple">
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                  <Option value="3"></Option>
                  <Option value="4"></Option>
                  <Option value="5"></Option>
                  <Option value="6"></Option>
                  <Option value="7"></Option>
                  <Option value="8"></Option>
                </Select>
              )}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('isMarried', {
                initialValue: true,
                valuePropName: 'checked'
              })(<Switch />)}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('birthday', {
                initialValue: moment('2019-09-09 12:00:59')
              })(<DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />)}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('address', {
                initialValue: ''
              })(
                <TextArea
                  autosize={{
                    minRows: 4,
                    maxRows: 6
                  }}
                />
              )}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('time')(<TimePicker />)}
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('userImg')(
                <Upload
                  listType="picture-card"
                  showUploadList={false}
                  action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                  onChange={this.handleChange}
                >
                  {this.state.userImg ? (
                    <img src={this.state.userImg} alt="" />
                  ) : (
                    <Icon type="plus" />
                  )}
                </Upload>
              )}
            </FormItem>
            <FormItem {...offsetLayout}>
              {getFieldDecorator('userKnow')(
                <Checkbox>
                  
                  <a href="/" alt="">
                    
                  </a>
                </Checkbox>
              )}
            </FormItem>
            <FormItem {...offsetLayout}>
              <Button type="primary" onClick={this.handleSubmit}>
                
              </Button>
            </FormItem>
          </Form>
        </Card>
      </div>
    );
  }
}

export default Form.create()(FormRegister);
