import React, { Component } from 'react';
import { Card, Table, Modal, Button, message } from 'antd';
import axios from '../../axios';
import Utils from '../../utils/utils';

export default class basicTable extends Component {
  state = {
    dataSource2: ''
  };
  params = {
    page: 1
  };
  componentDidMount() {
    const dataSource = [
      {
        id: '0',
        userName: 'Jack',
        sex: '1',
        state: '1',
        interest: '1',
        birthday: '2000-01-01',
        address: '',
        time: '09:00'
      },
      {
        id: '1',
        userName: 'Tom',
        sex: '1',
        state: '1',
        interest: '1',
        birthday: '2000-01-01',
        address: '',
        time: '09:00'
      },
      {
        id: '2',
        userName: 'Jack',
        sex: '1',
        state: '1',
        interest: '1',
        birthday: '2000-01-01',
        address: '',
        time: '09:00'
      }
    ];
    dataSource.map((item, index) => {
      return (item.key = index);
    });
    this.setState({
      dataSource
    });

    this.request();
  }

  /**
   * mock
   */
  request = () => {
    let _this = this;
    axios
      .ajax({
        url: '/table/list',
        data: {
          params: {
            page: this.params.page
          }
        }
      })
      .then(res => {
        if (res.code === 0) {
          res.result.list.map((item, index) => {
            return (item.key = index);
          });
          this.setState({
            dataSource2: res.result.list,
            pagination: Utils.pagination(res, current => {
              _this.params.page = current;
              this.request();
            }),
            // state
            selectedRowKeys: [],
            selectedRows: null
          });
        }
      });
  };

  onRowClick = (record, index) => {
    let selectKey = [index];
    Modal.info({
      title: '',
      content: `${record.userName}, ${record.interest}`
    });
    this.setState({
      selectedRowKeys: selectKey, // 
      selectedItem: record // 
    });
  };

  /**
   * 
   */
  handleDelete = () => {
    let rows = this.state.selectedRows;
    // id
    let ids = [];
    rows.map(item => {
      return ids.push(item.id);
    });
    Modal.confirm({
      title: '',
      content: `? ${ids.join(',')}`,
      onOk: () => {
        message.success('');
        // 
        this.request();
      }
    });
  };

  render() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '',
        dataIndex: 'userName'
      },
      {
        title: '',
        dataIndex: 'sex',
        render(sex) {
          return sex === 1 ? '' : '';
        }
      },
      {
        title: '',
        dataIndex: 'state',
        render(state) {
          let config = {
            '1': '',
            '2': '',
            '3': '',
            '4': 'FE',
            '5': ''
          };
          return config[state];
        }
      },
      {
        title: '',
        dataIndex: 'interest',
        render(interest) {
          let config = {
            '1': '',
            '2': '',
            '3': '',
            '4': '',
            '5': '',
            '6': '',
            '7': '',
            '8': ''
          };
          return config[interest];
        }
      },
      {
        title: '',
        dataIndex: 'birthday'
      },
      {
        title: '',
        dataIndex: 'address'
      },
      {
        title: '',
        dataIndex: 'time'
      }
    ];
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      type: 'radio',
      selectedRowKeys
    };
    const rowMoreSelection = {
      type: 'checkbox',
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({
          selectedRowKeys,
          selectedRows
        });
      }
    };
    return (
      <div>
        <Card title="">
          <Table
            bordered
            columns={columns}
            dataSource={this.state.dataSource}
            pagination={false}
          />
        </Card>
        <Card title="" style={{ margin: '15px 0' }}>
          <Table
            bordered
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={false}
          />
        </Card>
        <Card title="Mock-" style={{ margin: '15px 0' }}>
          <Table
            bordered
            rowSelection={rowSelection}
            onRow={(record, index) => {
              return {
                onClick: () => {
                  this.onRowClick(record, index);
                }
              };
            }}
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={false}
          />
        </Card>
        <Card title="Mock-" style={{ margin: '15px 0' }}>
          <div style={{ marginBottom: 15 }}>
            <Button onClick={this.handleDelete}></Button>
          </div>
          <Table
            bordered
            rowSelection={rowMoreSelection}
            onRow={(record, index) => {
              return {
                onClick: () => {
                  this.onRowClick(record, index);
                }
              };
            }}
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={false}
          />
        </Card>
        <Card title="Mock-" style={{ margin: '15px 0' }}>
          <Table
            bordered
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={true}
          />
        </Card>
      </div>
    );
  }
}
