import React, { Component } from 'react';
import { Card, Button, Radio } from 'antd';
import './ui.less';

export default class Buttons extends Component {
  state = {
    loading: true,
    size: 'default'
  };

  handleCloseLoading = () => {
    this.setState({
      loading: !this.state.loading
    });
  };

  handleChange = e => {
    this.setState({
      size: e.target.value
    });
  };

  render() {
    return (
      <div>
        <Card title="" className="card-wrap">
          <Button type="primary"></Button>
          <Button></Button>
          <Button type="dashed"></Button>
          <Button type="danger"></Button>
          <Button disabled></Button>
        </Card>
        <Card title="" className="card-wrap">
          <Button icon="plus" type="primary">
            
          </Button>
          <Button icon="edit"></Button>
          <Button icon="delete" type="danger">
            
          </Button>
          <Button shape="circle" icon="search"></Button>
          <Button type="primary" icon="search">
            
          </Button>
          <Button type="primary" icon="download">
            
          </Button>
        </Card>
        <Card title="Loading" className="card-wrap">
          <Button type="primary" loading={this.state.loading}>
            
          </Button>
          <Button
            type="primary"
            shape="circle"
            loading={this.state.loading}
          ></Button>
          <Button loading={this.state.loading}></Button>
          <Button shape="circle" loading={this.state.loading}></Button>
          <Button type="primary" onClick={this.handleCloseLoading}>
            
          </Button>
        </Card>
        <Card title="">
          <Button.Group>
            <Button type="primary" icon="left">
              
            </Button>
            <Button type="primary" icon="right">
              
            </Button>
          </Button.Group>
        </Card>
        <Card title="" className="card-wrap">
          <Radio.Group value={this.state.size} onChange={this.handleChange}>
            <Radio value="small"></Radio>
            <Radio value="default"></Radio>
            <Radio value="large"></Radio>
          </Radio.Group>
          <Button type="primary" size={this.state.size}>
            
          </Button>
          <Button type="dashed" size={this.state.size}>
            
          </Button>
          <Button type="danger" size={this.state.size}>
            
          </Button>
        </Card>
      </div>
    );
  }
}
